import { ApplianceCommand } from '../entities/ApplianceCommand';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Software update status type 
*/
export class UpdateStatus extends BaseEntity {
 
    // Update time [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Batch Id 
    public batchId: string;
 
    // The user Id who perform the command 
    public userId: string;
 
    // Account Id 
    public accountId: string;
 
    // Appliance Id 
    public applianceId: string;
 
    // Link to the command Id 
    public commandId: string;
 
    // Actual command 
    public command: ApplianceCommand;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


