import { BoundingBox } from '../common/BoundingBox';

/* 
   Cropped image with timestamp and bounding box 
*/
export class TimestampedCrop {
 
    // Crop timestamp [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Crop bounding box within the full frame 
    public boundingBox: BoundingBox;
 
    // Crop content: encoded base64 jpeg 
    public jpeg: string;
 
    constructor(timestamp?: number, boundingBox?: BoundingBox, jpeg?: string) { 
        this.timestamp = timestamp;
        this.boundingBox = boundingBox;
        this.jpeg = jpeg;
    }

}


