import { BaseEntity } from '../entities/BaseEntity';
import { VMSGatewayStatusCode } from '../enums/VMSGatewayStatusCode';

/* 
   VMS Gateway type
   VMS connection URI should be in the RFC-6570 format which includes schema, credentials, host, port and additional parameters as query string params 
*/
export class VMSGateway extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // VMS name 
    public name: string;
 
    // VMS type (e.g. Milestone, Genetec) 
    public type: string;
 
    // VMS connection URI (RFC 6570 format) 
    public uri: string;
 
    // VMS status 
    public status: VMSGatewayStatusCode;
 
    // VMS real time state bit mask flags 
    public state: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


