import { SensorStatusCode } from '../enums/SensorStatusCode';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Sensor health status 
*/
export class SensorStatus extends BaseEntity {
 
    // Sensor Id 
    public sensorId: string;
 
    // Account Id 
    public accountId: string;
 
    // Sensor state mask 
    public state: number;
 
    // UNDEFINED | ACTIVE | WARNING | ERROR | INACTIVE | SUSPENDED | PENDING 
    public status: SensorStatusCode;
 
    // Stream codec (i.e. h.264, h.265 etc) 
    public codec: string;
 
    // Stream resolution (i.e. 1280x720, 1920x1080 etc) 
    public resolution: string;
 
    // Stream framerate (frames per second) 
    public framerate: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


