
/* 
   A single entry in appliance configuration report that lists all the devices in the system with their desired vs. actual configuration 
*/
export class ApplianceConfigReport {
 
    // Account Id 
    public accountId: string;
 
    // Account Name 
    public accountName: string;
 
    // Appliance Id 
    public applianceId: string;
 
    // Appliance Name 
    public applianceName: string;
 
    // Machine Id (Serial Number) 
    public machineId: string;
 
    // Comma separated list of features groups 
    public groups: string;
 
    // Desired Configuration Version Id 
    public configId: string;
 
    // Desired Configuration Version Id 
    public versionId: string;
 
    // Actual Configuration Version Id 
    public actualConfigId: string;
 
    // Actual Configuration Version Id 
    public actualVersionId: string;
 
    constructor(accountId?: string, accountName?: string, applianceId?: string, applianceName?: string, machineId?: string, groups?: string, configId?: string, versionId?: string, actualConfigId?: string, actualVersionId?: string) { 
        this.accountId = accountId;
        this.accountName = accountName;
        this.applianceId = applianceId;
        this.applianceName = applianceName;
        this.machineId = machineId;
        this.groups = groups;
        this.configId = configId;
        this.versionId = versionId;
        this.actualConfigId = actualConfigId;
        this.actualVersionId = actualVersionId;
    }

}


