
/*  
*/
export class SysApplianceBatchUpgradeFindRequest {
 
    // Filter by account id (empty means no filter) 
    public accountId: string;
 
    // Filter by appliance id (empty means no filter) 
    public applianceId: string;
 
    // Filter by user id (empty means no filter) 
    public userId: string;
 
    // Filter by command id (empty means no filter) 
    public commandId: string;
 
    // Filter by batch id (empty means no filter) 
    public batchId: string;
 
    // Filter after the provided timestamp (epoch milliseconds) 
    public from: number;
 
    // Filter before the provided timestamp (epoch milliseconds) 
    public to: number;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(accountId?: string, applianceId?: string, userId?: string, commandId?: string, batchId?: string, from?: number, to?: number, sort?: string, page?: number, pageSize?: number) { 
        this.accountId = accountId;
        this.applianceId = applianceId;
        this.userId = userId;
        this.commandId = commandId;
        this.batchId = batchId;
        this.from = from;
        this.to = to;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


