
/* 
   Image of frame or crop with timestamp 
*/
export class TimestampedImage {
 
    // Image timestamp [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Image content: encoded base64 jpeg 
    public jpeg: string;
 
    constructor(timestamp?: number, jpeg?: string) { 
        this.timestamp = timestamp;
        this.jpeg = jpeg;
    }

}


