import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { DigitalIO } from '../entities/DigitalIO';


/**
 * Services for digital IO actions actions
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class ApplianceDigitalIOService {

  // URL to web api
  private baseUrl = '/appliance-dio';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create digital IO device configuration
   * @Return: EntityResponse<DigitalIO>
   */
  create(body?: DigitalIO) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update digital IO device configuration
   * @Return: EntityResponse<DigitalIO>
   */
  update(body?: DigitalIO) {
    return this.rest.put(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete digital IO device configuration
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single digital IO device configuration
   * @Return: EntityResponse<DigitalIO>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Find digital IO device configurations
   * @Return: EntitiesResponse<DigitalIO>
   */
  find(applianceId?: string, search?: string) {
    const params = new Array();		
    if (applianceId != null) { params.push(`applianceId=${applianceId}`); }
    if (search != null) { params.push(`search=${search}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

}
