import { EntityTypeCode } from '../enums/EntityTypeCode';

/*  
*/
export class IntegrationStatusFindRequest {
 
    // Filter by source (can be only EVENT or HEALTH_EVENT) 
    public source: EntityTypeCode;
 
    // Filter by entity id 
    public entityId: string;
 
    // Filter by integration target id 
    public integrationTargetId: string;
 
    // Filter by integration action id 
    public integrationActionId: string;
 
    constructor(source?: EntityTypeCode, entityId?: string, integrationTargetId?: string, integrationActionId?: string) { 
        this.source = source;
        this.entityId = entityId;
        this.integrationTargetId = integrationTargetId;
        this.integrationActionId = integrationActionId;
    }

}


